<script setup lang="ts">
import useList from "~/composables/use-list";
import usePagination from "~/composables/use-pagination";
import { ORDER, SORT } from "~/constants";
import SubscriptionAPI from "~/services/subscription";
import formatDate from "~/utils/format-date";
import getErrorLocalePathMessage from "~/utils/locale";
import { getPriceLabel } from "~/utils/subscription";

export interface UserSubscriptionsProps {
  limit?: number;
  active?: boolean;
  rootClass?: string;
  hideOnNoContent?: boolean;
  hasLoadMoreButton?: boolean;
}
const props = withDefaults(defineProps<UserSubscriptionsProps>(), {
  limit: 5,
  active: false,
});

const { t, te } = useI18n();
const getFormattedDate = (date: number) => formatDate(date, "d.L.yyyy");

const { items, loadMore, error, loading, canLoadMore, reset } = usePagination<IModelCommerce>({
  immediate: true,
  pageSize: props.limit,
  loadItemsFn: (page: number, pageSize: number) => SubscriptionAPI.loadUserSubscriptions({ page, pageSize, sort: SORT.added, order: ORDER.desc }, props.active),
});
const filteredItems = computed(() => props.active ? items.value : items.value.filter(item => !item.isActive || item.isCanceled));
const listItems = useList(filteredItems, { limit: props.hasLoadMoreButton ? undefined : props.limit });
const subscriptionsTitle = computed(() => props.active ? t("labels.active") : t("labels.inactive"));
const errorMessage = computed(() => error.value && getErrorLocalePathMessage(error.value, { t, te }).message);
</script>

<template>
  <BaseList
    root-class="!p-0"
    grid-class="form-column"
    :items="listItems"
    :loading="loading"
    :error="errorMessage"
    :limit="props.limit"
    :hide-on-no-content="props.hideOnNoContent"
    :has-load-more-button="props.hasLoadMoreButton && canLoadMore && Boolean(listItems.length)"
    @click-load-more="loadMore()"
    @visible="reset()"
  >
    <template #start>
      <div v-if="!props.active" class="hr my-6" />

      <div class="text-heading-3 text-secondary mb-6">
        {{ subscriptionsTitle }}
      </div>
    </template>
    <template #item="{ item, index }">
      <BaseButton
        :key="item.id"
        class="block w-full"
        tag="nuxt-link"
        type="icon"
        :name="`item-subscription-${index}`"
        :to="`/account/subscription/${item.id}/`"
      >
        <CardSubscription
          class="w-full mb-6"
          :title="item.title"
          :description="item.description"
          :price="getPriceLabel(item.price, item.currency)"
          :next-charge-date="getFormattedDate(item.endDate)"
          :expiration-date="getFormattedDate(item.endDate)"
          :active="item.isActive && !item.isCanceled"
          :auto-renew="item.autoRenew"
        />
      </BaseButton>
    </template>
    <template #skeleton>
      <CardSubscriptionSkeleton class="w-full mb-6" />
    </template>
    <template #empty>
      <div v-if="props.active" class="flex flex-col justify-center items-center mt-6 gap-4">
        <IconSubscription fill="var(--base-icon-color)" />
        <div class="text-base text-center text-secondary whitespace-pre-wrap">
          {{ t('pages.account_subscription.no_subscriptions_text') }}
        </div>
      </div>
    </template>
    <template #end>
      <slot name="end" />
    </template>
  </BaseList>
</template>
