<script setup lang="ts">
import useAsync from "~/composables/use-async";
import usePagination from "~/composables/use-pagination";
import BillingAPI from "~/services/billing";
import SubscriptionAPI from "~/services/subscription";
import getErrorLocalePathMessage from "~/utils/locale";

const { t, te } = useI18n();
const toaster = useToast();
const activeTabIndex = ref("0");
const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};
const { run: loadUserCreditCard } = useAsync(BillingAPI.load, { onError });
const { items: commerceOptions, loading } = usePagination<IModelCommerce>({
  immediate: true,
  loadItemsFn: () => SubscriptionAPI.load(),
});

onMounted(() => loadUserCreditCard());
definePageMeta({ layout: "home", middleware: "auth" });
</script>

<template>
  <div v-if="loading" class="text-main">
    {{ t("labels.loading") }}
  </div>
  <template v-else>
    <BaseTabs v-model:active-index="activeTabIndex" root-class="text-xs sm:text-sm md:text-base">
      <BaseTab :heading="t('pages.account_subscription.tab_my_subscriptions')">
        <UserSubscriptions has-load-more-button active />
        <UserSubscriptions has-load-more-button hide-on-no-content />
      </BaseTab>
      <BaseTab :heading="t('pages.account_subscription.tab_all_subscriptions')">
        <AccountCommerceOptions :options="commerceOptions" />
      </BaseTab>
    </BaseTabs>
  </template>
</template>
