<script setup lang="ts">
export interface CardSubscriptionProps {
  title: string;
  price?: string;
  expirationDate?: string;
  nextChargeDate?: string;
  active?: boolean;
  disabled?: boolean;
  autoRenew?: boolean;
}
const { t } = useI18n();
const props = defineProps<CardSubscriptionProps>();
const dateTitle = computed(() => {
  const label = !props.active
    ? t("pages.account_subscription.subscription_expiration_date")
    : props.autoRenew
      ? t("pages.account_subscription.subscription_next_charge_date")
      : t("pages.account_subscription.subscription_due_date");

  const date = props.active ? props.nextChargeDate : props.expirationDate || "---";

  return `${label}: ${date}`;
},
);
</script>

<template>
  <div class="cardSubscription" :class="{ cardSubscriptionActive: props.active }">
    <div class="flex items-center justify-between mb-4">
      <div class="text-heading-3 text-main min-h-[1.5rem]">
        {{ props.title }}
      </div>
      <IconChevronRight class="relative z-10 text-main cursor-pointer" />
    </div>
    <div class="flex items-center justify-between">
      <div class="text-body-medium min-h-[1.5rem] text-secondary">
        {{ dateTitle }}
      </div>
      <div class="text-body-medium min-h-6 text-secondary uppercase">
        {{ props.price }}
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.cardSubscription {
  @apply relative py-5 pl-5 pr-4 overflow-hidden;

  background-color: var(--base-card-bgColor);
  border-radius: var(--base-border-radius);

  &::before {
    @apply absolute top-0 left-0 h-full w-1;
    content: '';
    background-color: var(--base-card-bgSecondaryColor);
  }

  &.cardSubscriptionActive::before {
    background-color: var(--base-primary-color);
  }
}
</style>
