<script lang="ts" setup>
import useAsync from "~/composables/use-async";
import BillingAPI from "~/services/billing";
import { getCallbackCommerceUrl } from "~/utils/commerce";
import getErrorLocalePathMessage from "~/utils/locale";

export interface AccountListCommerceProps {
  options?: IModelCommerce[];
}

const route = useRoute();
const toaster = useToast();
const router = useRouter();
const { t, te, locale } = useI18n();
const appConfig = useRuntimeConfig().public;
const props = defineProps<AccountListCommerceProps>();

const isHebrew = computed(() => locale.value === "he");

const isAccessCodesAvailable = appConfig.APP_CUSTOMER.enable_accessCodes;
const hasMercadoPagoPayment = appConfig.APP_CUSTOMER.enable_mercadopago_payment;

const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};

const { loading: loadingInitialization, run: initPayment } = useAsync(BillingAPI.initPayment, { onError });

const onClickBuyCommerce = (subscription: IModelCommerce) => {
  if (hasMercadoPagoPayment)
    return router.push({ path: `${route.fullPath}buy/${subscription.id}/` });

  const params = {
    redirectURL: route.fullPath,
    title: subscription.title,
    productId: subscription.id,
  };

  const callBackURL = getCallbackCommerceUrl(params);
  return initPayment(subscription.id, callBackURL, isHebrew.value ? "he" : "en");
};
</script>

<template>
  <CommerceAccessCodeRedeemUser v-if="isAccessCodesAvailable" />

  <ListSubscriptions
    :hide-on-no-content="isAccessCodesAvailable"
    :no-content-text="t('pages.account_subscription.list_subscriptions_empty')"
    class="mt-6"
    :subscriptions="props.options"
    :disabled="loadingInitialization"
    @buy-commerce="onClickBuyCommerce"
  >
    <template v-if="isAccessCodesAvailable && props.options?.length" #start>
      <div class="mb-6 text-secondary text-body-medium">
        {{ t('pages.account_subscription.or_select_plan') }}
      </div>
    </template>
  </ListSubscriptions>
</template>
